import {UserAPI} from "../../DAL/API/api";
import {Dispatch} from "redux";
import {AppActionsType} from "../Store/redux-store";

export type IpDataActionType = SetIpDataAT

type IpDataType = {
    ipData: {
        businessName: string
        businessWebsite: string
        city: string
        continent: string
        country: string
        countryCode: string
        ipName: string
        ipType: string
        isp: string
        lat:string
        lon: string
        org: string
        query: string
        region:string
        status: string
    }
    privacyData: {
        vpn: boolean,
        proxy: boolean,
        tor: boolean,
        hosting: boolean
    }
}

let initialState: IpDataType = {
    ipData: {
        businessName: "",
        businessWebsite: "",
        city: "",
        continent: "",
        country: "",
        countryCode: "",
        ipName: "",
        ipType: "",
        isp: "",
        lat:"",
        lon: "",
        org: "",
        query: "",
        region:"",
        status: "",
    },
    privacyData: {
        vpn: false,
        proxy: false,
        tor: false,
        hosting: false
    }
}

export const iPdataReducer = (state:IpDataType = initialState, action: IpDataActionType):IpDataType => {
    switch (action.type) {
        case SET_DATA: {
            return {
                ...state,
                ...action.payload
            }
        }
        default:
            return state
    }
}
//--------------------------------------SET-IP-DATA-AC-------------------------------
const SET_DATA = "iPdataReducer/SET_DATA"
type SetIpDataAT = {
    type: typeof SET_DATA
    payload: IpDataType
}
export const setIpDataAC = (payload: IpDataType): SetIpDataAT => ({
    type: SET_DATA,
    payload: payload
})
//--------------------------------------SET-IP-DATA-TC-------------------------------
export const setIpDataTC = () => async (dispatch: Dispatch<AppActionsType>) => {
    let response = await UserAPI.getIpData()
    dispatch(setIpDataAC(response));
}
